.radio-style {
    width: 20px;
    height: 20px;

    background-color: #000;
    margin-right: 10px;
    border-radius: 50%;
    position: relative;
}
.radio-style:before,.radio-style:after {
    content: '';
    display: block;
    position: absolute;
    border-radius: 50%;
    -webkit-transition: .3s ease;
    transition: .3s ease;
}
.radio-style:before {
    top: 0px;
    left: 0px;
    width: 18px;
    height: 18px;
    background-color: #fff;
    border: 1px solid #7895ff;
}
.radio-style:after {
    top: 6px;
    left: 6px;
    width: 8px;
    height: 8px;
    background-color: #fff; 
}
.radio-style:checked:after {
    top: 4px;
    left: 4px;
    width: 12px;
    height: 12px;
    background-color:#7895ff;
}
.radio-style:checked:before {
    border-color:#7895ff;
}
